
.bg {position: relative;background-image: url(../assets/images/live_player_bg.jpg);height: 100px;
}

/*.bg img{width: 100%}*/
.bg h3,.bg p,.bg span{color: #fff
}
.bg h3{line-height: 50px;margin: 0 auto;
}
.bg span{font-size: 12px;position: absolute;bottom: 5px;left: 5px;
}
.page4{background: #fff;margin: 0 auto;
}
.txt{ background: #eee; font-size: 14px;color:#fff;width: 150px;height: 30px;display: inline-block;text-align: center;line-height: 30px;margin-left: 70px;
}
.page4 .pic1 {width: 100%;height: 100px;position: relative;
}
.page4 .pic1 img{ width: 100% ;
}
.page4 .pic1 p{position: absolute;bottom: 32px;left: 17px;color: #fff
}
.ys{height: 50px;
}
.ys p{float: left;padding-left: 20px;
}
.ys .ht{width: 40%;border-bottom: 1px solid gray;height: 1px;float: left;padding-top: 10px
}
.ys .ht2{width: 40%;border-bottom: 1px solid gray;height: 1px;float: right;padding-top: 10px
}
.ys p{float: left;padding-left: 20px;
}
.lt{ float: left; font-size: 14px;
}
.lt p{float: left;color: gray;padding-left: 5px
}
.page4 .gao{height: 49px;
}
.page4 .lt{ float: left; font-size: 14px;
}
.page4 .lt p{float: left;color: gray;padding-left: 5px
}
.page4 .lt img{width: 25px;padding-left: 20px;padding-top: 10px;float: left;
}
.page4 .txt{ background: #eee; font-size: 14px;color:#fff;width: 150px;height: 30px;display: inline-block;text-align: center;line-height: 30px;position: absolute;top: 5px;right: 20px;
}



