
.clear1 {clear: both;
}
.Gz{background: #fff;border-radius: 10px;width: 90%;margin: 10px auto; box-shadow: 2px 2px 10px rgba(0,0,0,.5)
}
.Gz .toux,.Gz .text {padding-top: 10px;position:relative;
}
.Gz .toux { width: 20%;float: left;
}
.Gz .toux img{ width: 60px;height:60px; border-radius: 50%;
}
.Gz .text {float: left;width:75%;text-align: left;margin-left: 10px
}
.Gz .text h4{font-size: 20px;
}
.Gz .text span{font-size: 12px;line-height: 25px;color: gray
}
.Gz .text p{font-size: 16px;line-height: 40px;
}
.Gz .text .pic img{width: 15px;
}
.Gz .text .pic{ margin: 10px 0; color:gray;font-size: 12px
}
.Gz .text .pic img:nth-of-type(2){ padding-left: 40px;
}
.text .guanz{width: 60px;height: 25px;background:  #42bd56;text-align: center;font-size: 14px;color: #fff;line-height: 25px;border-radius: 10px;position: absolute;top: 20px;right: 30px;
}
.text i {position: absolute;top: 8px;right: 6px;font-style: normal;color: gray;font-size: 12px
}



