
.n_lt{float: left;width: 195px;padding-left:10px;
}
.news{overflow:hidden; width: 100%;text-align: left;border-bottom: 1px solid #eee;padding-top: 10px;background: #fff;
}
.news h3{display: inline-block;
}
.news img{width: 100px;float: right;padding-right:10px;
}
.n_lt span{-webkit-transform: rotate(90deg);transform: rotate(90deg);display: inline-block;color: #eee
}
.news p{font-size: 12px;color:gray;line-height: 20px;
}
.news p:nth-of-type(2){padding-bottom: 10px;color:lightgray;
}



