
.homeHeader{width: 100%;height: 48px;border-bottom: 1px solid gray;line-height: 48px;position: fixed;z-index: 2
}
.zhanwei{height: 64px;
}
.HomeWrap{background: #eee
}
.search{width: 85%
}
.search input{height: 30px;border-radius: 5px;border: none;width: 100%;text-indent: 3em;margin-left: 10px
}
.search img:nth-of-type(1){width: 25px;position: absolute;top: 12px;left: 15px
}
.search img:nth-of-type(2){width: 25px;position: absolute;top: 12px;right: 55px
}
.rtImg img{width: 25px;position: absolute;top: 12px;right: 12px
}
.HomeWrap .Hot{width: 100%;
}
.HomeWrap .Hot_t p{color: orange;padding:10px 0;text-align: left;
}
.HomeWrap .Hot_t span{padding-right: 20px;font-weight:800;
}
.HomeWrap .tj{width: 100%;
}
.HomeWrap .tj_t p{color: skyblue;padding:10px 0;text-align: left;
}
.HomeWrap .tj_t span{padding-right: 20px;font-weight: 800
}
.HomeWrap .yl{width: 100%;
}
.HomeWrap .yl_t p{color: lightgreen;padding:10px 0;text-align: left;
}
.HomeWrap .yl_t span{padding-right: 20px;font-weight: 800
}
.banner1{margin-top: 49px;
}
.HomeWrap .Hot,.tj,.yl{margin-top: 20px;background-color: #fff;border-top: 1px solid #eee;box-shadow: 0 0 5px rgba(0,0,0,.5)
}
.HomeWrap .Hot{margin-top: -5px;
}
.HomeWrap .home_p{font-size:14px; overflow: hidden; color:#999;padding-top: 10px;width: 100%;display: -webkit-box;text-overflow: ellipsis;-webkit-line-clamp:3;-webkit-box-orient:vertical;
}
/*
	webkit-line-clamp:用来限制在一个块元素像是的文本的行数.为了实现该效果,它需要组合其他的
	display: -webkit-box; 必须结合的属性,将对象作为弹性伸缩盒子模型显示
	-webkit-box-orient:vertical;必须结合的属性,设置或检索伸缩盒子对象

*/
.HomeWrap .hello{
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;
}
.HomeWrap .hello img{
	height: 100%;
	width: 100%;
}



