
.news1 .text{
}
.news1 .btn{padding:0 5px;margin-left: 5px;color:#666;border:1px solid #ccc;width:50px;border-radius: 5px;
}
.news1 .btn span{padding-right:10px;color:#666;font-weight: 700
}
.news1 .n_lt{float: left;width: 195px;padding-left:10px;
}
.news1{overflow:hidden; width: 100%;text-align: left;padding-top: 10px;background: #fff;
}
.news1 h3{display: inline-block;padding-top: 20px;margin-bottom: 20px;
}
.news1 img{width: 150px;float: right;padding-right:10px;
}
.news1 .n_lt span{-webkit-transform: rotate(90deg);transform: rotate(90deg);display: inline-block;color: #eee
}
.news1 p{font-size: 14px;color:gray;line-height: 20px;width: 200px;display: inline-block;
}
.news1 p:nth-of-type(2){padding-bottom: 10px;color:lightgray;
}
.news1 .gaodu{height: 48px;
}
.news1{margin: 0;padding:0;
}
.news1 .ltImg img{ width: 25px;position: absolute;top: 12px;right: 50px
}
.news1 .rtImg img{width: 25px;position: absolute;top: 12px;right: 12px
}
.news1 .height{height: 48px;
}



